import { getScenarios } from "../scripts/connect-firebase.js";
import { useState, useEffect, useContext } from "react";
import { AuthContext } from "@meddbriefer/mdb-auth"
import { useHistory } from "react-router-dom";
import { permanentlyDeleteScenario } from '../scripts/connect-firebase';
import Header from './Header';

const ScenariosList = () => {
	const [scenarios, setScenarios] = useState(null);
	const { currentUser, signOutUser } = useContext(AuthContext);
	const LOADING = <p>Loading...</p>;
	let history = useHistory();

	useEffect(() => {
		const getRes = async () => {
			const res = await getScenarios();
			setScenarios(res);
		};
		getRes();
	}, []);

	return (
		<>
			<Header user={currentUser} signOut={signOutUser} />

			<div className="scenario-list">
				<h1 id='scenario-list-title'>Scenario List</h1>

				<div className="new-scenario-bttn list-scenario-item" onClick={() => history.push('/new-scenario')}>
					<h1>+</h1>
					<p>Create a New Scenario</p>
				</div>

				{scenarios 
					? scenarios.map((item) => (
							<ListItem item={item} key={`${item.name}-list`}/>))
				 	: LOADING }
			</div>
		</>
	);
};

const ListItem = ({ item }) => {
	let history = useHistory();
	return (
		<div className="list-scenario-item">
			<h1 id="title">{item.name}</h1>
			<p>{item.scenarioType}</p>
			<p>Info: {item.info.instructorInformation ?? "Not given."}</p>

			<button type="button" onClick={() => history.push(`/edit/${item.name.replaceAll(' ', '_')}`)}>Edit</button>	
			<button type="button" onClick={() => deleteScenario(item.name)}>Delete</button>
		</div>
	);
};

function deleteScenario(id) {
	if( window.confirm('Are you sure you want to delete this scenario? This cannot be undone!') ) {
		permanentlyDeleteScenario(id);
		window.location.reload();
	} else {
		console.log('Delete of', id, 'cancelled');
	}
}

export default ScenariosList;
