const Vitals = ({ scenario }) => {
	return (
		<div className="info-box vitals">
			<VitalsBox type="init" header="Initial" scenario={scenario} />
			<VitalsBox type="goodVitals" header="Good Reassessment" scenario={scenario} />
			<VitalsBox type="badVitals" header="Bad Reassessment" scenario={scenario} />
		</div>
	);
};

const VitalsBox = ({ scenario, header, type }) => {
	let prefix = (type === 'init') ? 'initialVitalSigns' : 'reassessmentVitals';
	let suffix = (prefix === 'initialVitalSigns') ? null : type;
	// Prefixes and suffixes needed for path
	return (
		<div className="vital-wrapper">
			<h1> {`${header} Vitals:`} </h1>
			<div className="vitals-box">
				<Vital label='BP' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='P' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='R' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='Skin' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='Spo2' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='ETCO2' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='GCS' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='Glucose' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='Pain' prefix={prefix} suffix={suffix} scenario={scenario} />
				<Vital label='Temp' prefix={prefix} suffix={suffix} scenario={scenario} />
			</div>
		</div>
	);
};

function Vital ({scenario, label, prefix, suffix}) {
	// Drop suffix if there is none (prevent tailing hyphen);
	let path = suffix ? `${prefix}-${label}-${suffix}` : `${prefix}-${label}`;
	
	return (
		<label>
			{label}:
			<input
				id={`${path}`}
				type="text"
				defaultValue={suffix ? 
					scenario[prefix][label][suffix] : 
					scenario[prefix][label]}
			/>
		</label>
	);
}

export default Vitals;
