import { useHistory, useLocation } from 'react-router-dom';

const Header = ({user, signOut}) => {
  let history = useHistory();
  let location = useLocation();
  return (
    <div className="title-bar">
      <h1>MedDBriefer</h1>

      {location.pathname !== '/' && 
      <button type="button" className="back-bttn" onClick={() => history.goBack()}>Back</button>}

      {user && 
      <button type="button" className="sign-out-bttn" onClick={() => signOut()}>Sign out</button>}
    </div>
  );
}

export default Header;