import React from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

//import "bootstrap/dist/css/bootstrap.min.css"

import HomePage from "./components/HomePage";
import ScenarioForm from "./components/ScenarioForm";
import InterventionForm from "./components/InterventionForm";
import ScenariosList from "./components/ScenariosList";

import { auth } from "@meddbriefer/mdb-firebase";

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";

const App = () => {
  return (
    <AuthProvider auth={auth} onSignedInPath={"/home"} onSignedOutPath={"/"}>
      <Router>
        <Switch>
          <Route exact path="/" component={HomePage} />
          <PrivateRoute exact path="/scenarios" component={ScenariosList} />
          <PrivateRoute exact path="/new-scenario" component={ScenarioForm} />
          <PrivateRoute path="/edit/" component={ScenarioForm} />
          <PrivateRoute exact path="/intervention-form" component={InterventionForm} />
        </Switch>
      </Router>
    </AuthProvider>
  );
};

export default App;
