import "../styles/home-styles.scss";
import { Link } from "react-router-dom";
import { useContext } from "react";
import { AuthContext, AuthFlow } from "@meddbriefer/mdb-auth"
import Header from './Header';

const HomePage = () => {
  const { currentUser, signOutUser } = useContext(AuthContext);

  // manage auth stuff ourselves
  if (!currentUser) {
      return <AuthFlow heading={null} />
  }

  return (
    <>
      <Header user={currentUser} signOut={signOutUser} />

      <div className="nav-wrapper">
        <Link to="/scenarios">
          <div className="home-nav-button">Scenario Forms</div>
        </Link>
        <Link to="/intervention-form">
          <div className="home-nav-button">Intervention Forms</div>
        </Link>
      </div>
    </>
  );
};

export default HomePage;
