import React, { useState, useEffect } from "react";
import update from 'immutability-helper';
import { useHistory } from "react-router-dom";
import BasicInfo from "./BasicInfo.js";
import Steplist from "./Steplist.js";
import Vitals from "./Vitals";
import SAMPLE from "./Sample.js";
import {
	publishScenario,
	getScenarioWithName,
} from "../scripts/connect-firebase.js";
import "../styles/form-styles.scss";

/************************ IMPORTANT!! ***************************
   ID's of each HTML input element are very specifically set.
   Do not change or it will break the form's saving and loading.
*****************************************************************/

const ScenarioForm = (props) => {
	const LOADING = <p>Loading...</p>;
	const [scenario, setScenario] = useState(null); // current state of scenario form
	const [view, setView] = useState(LOADING); // current view
	const [loaded, setLoaded] = useState(false); // scenario loaded
	const [edit, scenarioId] = selectScenario(); // edit mode & scenario id
	let history = useHistory();

	// Load and apply selected scenario
	useEffect(() => {
		async function loadScenario() {
			let result = await getScenarioWithName(scenarioId);
			setScenario(result);
			setLoaded(true);
		}
		loaded 	? setView(<BasicInfo scenario={scenario} edit={edit} />)
						: loadScenario();
	// eslint-disable-next-line 
	}, [loaded, scenarioId]);

	function submitForm(e) {
		if (
			//Prevent overwriting blank-trauma
			scenario.name.trim() === "" ||
			scenario.name.trim() === "blank-trauma" ||
			scenario.name.includes("_")
		) {
			alert("Scenario needs a new name! And cannot include underscores!");
		} else if (window.confirm("All empty items will be filled with default information. Submit?")) {
			publishScenario(scenario.name, scenario); // create new
			history.push("/"); // Send home once submitted
		} else {
			console.log("Submit cancelled.");
		}
	}

	return (
		<>
			<nav id="tab-selector">
				<div className="tab-button" onClick={() => setView(<BasicInfo scenario={scenario} edit={edit} />)}> Info </div>
				<div className="tab-button" onClick={() => setView(<SAMPLE scenario={scenario} />)}> Sample </div>
				<div className="tab-button" onClick={() => setView(<Vitals scenario={scenario} />)}> Vitals </div>
				<div className="tab-button" onClick={() => setView(<Steplist scenario={scenario} />)}> Steplist </div>
				<button id="submit-next" onClick={() => submitForm()}> Submit → </button>
			</nav>

			<form id="scenario-form" className="scenario-form" onChange={e => handleUpdate(e)}>
				{ view }
			</form>
		</>
	);

	function handleUpdate(e) {
		// console.log(e);
		let input = e.target.value; // Get user text input
		let id = e.target.id;
		let path = id.split('-');
		if (path[0] === 'assessmentFindings') path = [path[0], id.split('assessmentFindings-').slice(1).toString()];
		else if (path[0] === 'interventions') path = [path[0], id.split('interventions-').slice(1).toString()];
		// Assessment findings/interventions id's indclude hypens. This fixes that. slice(1) removes leading "" from split
		
		//if (input.trim() === "") input = "Unremarkable"; // Base case
		let newScenario;
		// Handle different object depths
		switch (path.length) {
			case 1: newScenario = update(scenario, { [path[0]]: {$set: input} }); break;
			case 2: newScenario = update(scenario, { [path[0]]: { [path[1]]: {$set: input} }}); break;
			case 3: newScenario = update(scenario, { [path[0]]: { [path[1]]: {[path[2]]: {$set: input} }}}); break;
			default: console.log('Path length:', path.length); break;
		}
		
		//console.log(newScenario);
		setScenario(newScenario); // apply changes
	}

	function selectScenario() {
		let edit = props.location.pathname.includes("/edit/");
		let url = props.location.pathname.replace("/edit/" , "");
		let scenarioId = url.replaceAll("_", " ");
		scenarioId = (scenarioId === "/new-scenario") ? "blank-trauma" : scenarioId;
		return [edit, scenarioId];
	}
};

export default ScenarioForm;
